<template>
  <div class="container">
    <!-- 顶部搜索 -->
    <navbar class="search">
      <i class="iconfont iconjiantou2" slot="back" @click="$router.back()"></i>
      <van-search
        v-model="value"
        placeholder="51岁怎么样补交社保"
        slot="title"
        @keydown.enter="handlesearch"
      />
      <div slot="info" class="seach-text" @click="handlesearch">搜索</div>
    </navbar>
    <!-- 历史记录 -->
    <div class="search-history">
      <h4 class="title">历史记录</h4>
      <span class="icon-info iconfont icon-qingkongshanchu" @click="handeldel"></span>
    </div>
    <!-- 历史记录 -->
    <div class="serch-list">
      <div class="list-item" v-for="(item,index) in history" :key="index" @click="fastSearch(item)">
        <p>{{item}}</p>
        <i class="iconfont iconicon-test" @click="close(index)"></i>
      </div>
    </div>
    <!-- 热门搜索 -->
    <div class="hot">
      <h4 class="title">热门搜索</h4>
      <div>
        <div class="list-item">办公室宵夜否认解散</div>
        <div class="list-item">办公室宵夜否认解散</div>
        <div class="list-item">办公室宵夜否认解散</div>
        <div class="list-item">办公室宵夜否认解散</div>
      </div>
    </div>
    <!-- toast 浮层 -->
    <!-- <keep-alive> -->
    <div class="toast" v-if="showLayer&&value">
      <div class="list-item" v-for="(item,index) in result" :key="index" @click="todetail(item.id)">
        <p class="text">{{item.title}}</p>
        <i class="iconfont iconjiantou1"></i>
      </div>
    </div>
    <div class="toast empty" v-if="nodata">
      <p>没有搜索到</p>
    </div>
    <!-- </keep-alive> -->
  </div>
</template>

<script>
import navbar from "@/components/navbar";
import { Dialog } from "vant";
export default {
  // 组件进入时候触发这个路由守卫
  // to: 代表你即将要访问的页面
  // from：代表你从哪里来
  // next：必须要调用，next就类似于你nodejs的中间件，调用才会加载后面的内容
  // beforeRouteEnter(to, from, next) {
  //   // 通过 `vm` 访问组件实例, vm就是this
  //   next(vm => {
  //     // 如果是从首页进来的，就把数据初始化
  //     if (from.path === "/index") {
  //       // 隐藏浮层
  //       // vm.showLayer = true;
  //       // 清空关键字
  //       // vm.value = "";
  //       console.log("123");
  //     }
  //   });
  // },
  data() {
    return {
      // 是否展示浮层
      showLayer: false,
      nodata: false,
      value: "",
      //   搜索的历史记录数组
      history: JSON.parse(localStorage.getItem("history")) || [],
      result: []
    };
  },
  components: {
    navbar
  },
  watch: {
    value() {
      if (this.value == "") {
        this.showLayer = false;
        this.nodata = false;
      } else {
        this.showLayer = true;
      }
    }
  },
  methods: {
    todetail(id) {
      // 动态路由传参,跳转到新闻详情
      this.$router.push({
        path: `/detail/${id}`
      });
    },
    //   清空历史记录
    handeldel() {
      if (this.history.length > 0) {
        Dialog.alert({
          title: "提示",
          message: "确定要清空所有历史记录吗？"
        }).then(() => {
          this.history = [];
          localStorage.removeItem("history");
        });
      }
    },
    close(index) {
      this.history.splice(index, 1);
      localStorage.setItem("history", JSON.stringify(this.history));
    },

    //   处理搜索的内容
    handlesearch() {
      if (this.value == "") return;
      //   把当前的搜索内容添加到数组前
      this.history.unshift(this.value);
      //   实现数据去重
      this.history = [...new Set(this.history)];
      localStorage.setItem("history", JSON.stringify(this.history));
      //   搜索请求
      this.$axios({
        url: "/post_search",
        params: {
          keyword: this.value
        }
      }).then(res => {
        const { data } = res.data;

        if (data.length > 0) {
          this.result = data;
          this.showLayer = true;
        } else {
          // 没有搜索到
          this.result = [];
          this.showLayer = false;
          this.nodata = true;
        }
      });
    },
    fastSearch(item) {
      this.value = item;
    }
  }
};
</script>

